.social {
  display: flex;
  justify-content: space-between;
}

.social__item {
  display: flex;
  align-items: center;
  margin-left: 20px;
  opacity: 0.3;
  transition: .3s opacity ease;

  &:first-child {
    margin-left: 0;
  }

  &:hover {
    opacity: 1;
  }
}

.social__link {
  width: 20px;
  height: 20px;

  &--vk {
    width: 25px;
  }
}

@each $icon in (vk, fb, inst, twitter) {
  .social__link--#{$icon} {
    background: url(../images/sprite.svg##{$icon}) no-repeat center / cover;
  }
}