.hamburger.active > .hamburger__elem:nth-child(3) { transform: rotate(-45deg); }

.button { color: #fff; background-color: #e45028; border-radius: 5px; text-transform: uppercase; font-weight: 500; padding: 12px 15px; outline: none; border: 0; cursor: pointer; transition: .3s background-color ease; }

