.accordion { color: #fff; }

.accordion__item { margin-bottom: 35px; transition: .3s all ease; }

.accordion__item.active > .accordion__head { color: #f9b43b; }

.accordion__item.active > .accordion__head::before { transform: rotate(180deg); }

.accordion__item.active > .accordion__card { height: 100%; padding: 18px; }

